import { Component, OnInit } from '@angular/core';
import { ValidateService } from '../validateService/validate.service';
import { Injectable } from '@angular/core';
import { NgFlashMessageService } from 'ng-flash-messages';
import { HttpClient, HttpHeaders } from "@angular/common/http";
import { Router } from '@angular/router';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {

  name:String;
  uname:String;
  email:String;
  passw:String;

  constructor(private vs: ValidateService, private ngFlashMessageService: NgFlashMessageService, private http:HttpClient, private router:Router) { }

  ngOnInit() {
    
  }

  onRegisterSubmit(){
    var user = ({
      name:this.name,
      email:this.email,
      uname:this.uname,
      passw:this.passw,
    })



    if(!this.vs.validateRegister(user)){
      var msg = "please fill in all fields";
      this.ngFlashMessageService.showFlashMessage({
        messages: [msg],
        dismissible: true, 
        timeout: false,
        type: 'danger'
      });
      return false;
    }
    else if(!this.vs.validateEmail(user.email)){
      var msg = "please use a valid email";
      this.ngFlashMessageService.showFlashMessage({
        messages: [msg],
        dismissible: true, 
        timeout: false,
        type: 'danger'
      });
      return false;
    }else{
      var headers = new HttpHeaders({
        "Content-Type":'application/json'
      })
  
      this.http.post("/users/add", user, {headers:headers}).subscribe(res => {
        this.ngFlashMessageService.showFlashMessage({
          messages: ["You may now log in"],
          dismissible: true, 
          timeout: false,
          type: 'success'
        });
        this.router.navigate(["login"])
      })
    }
  }

}
